const generateId = () => "rp-" + Math.random().toString(36).slice(2, 10)

// The casting logic could be removed with an explicit check
const NOT_PREFIX = "not-"
const removeNot = (str: string) => str.replace(NOT_PREFIX, "")
const addNot = (str: string) => NOT_PREFIX + str

const removeNotFromType = (str: "not-and" | "not-or") => removeNot(str) as "and" | "or"

const removeNotFromOperation = (str: "not-in" | "not-all") => removeNot(str) as "in" | "all"

const addNotToType = (str: "and" | "or") => addNot(str) as "not-and" | "not-or"

const addNotToOperation = (str: "in" | "all") => addNot(str) as "not-in" | "not-all"

export { generateId, removeNotFromType, removeNotFromOperation, addNotToOperation, addNotToType }
